/* Extra small devices (phones, 600px and down)

*/
@media only screen and (min-width: 350px) {
  .welcomeTxt {
    top: 10px;
    text-align: justify;
    font-size: 55px;
    font-weight: 600;
    color: #36454f;
    /* padding: 10%; */
  }
  .docImg {
    height: 380px;
    width: 250px;
  }
  .container {
    position: relative;
  }

  .text-block {
    position: absolute;
    /* bottom: 240px; */
    /* padding-left: 20px; */
    /* bottom: 20px;
    right: 20px; */
    /* background-color: black;
    color: white; */
    /* padding-left: 20px;
    
    padding-right: 20px; */
  }

  .docAnim {
    position: absolute;
    top: 160px;
    right: 30px;
  }

  .lapAnim {
    position: absolute;
    left: 130px;
    top: 280px;
  }
  .ehms {
    font-size: 45px;
    text-align: center;
    color: #0039a6;
    font-weight: 900;
  }
}
