@import url("https://fonts.googleapis.com/css2?family=Cinzel:wght@500&family=Crimson+Text:wght@700&family=Dancing+Script:wght@700&family=Heebo:wght@100&family=Lato:wght@300;400&family=Montserrat:ital,wght@0,300;0,400;1,400&family=Nanum+Gothic&family=Noto+Sans+HK&family=Open+Sans:wght@300&family=Plus+Jakarta+Sans:wght@200&family=Poppins:wght@200;600&family=Prompt:wght@300&family=Questrial&family=Wix+Madefor+Text&display=swap");

@media only screen and (min-width: 350px) {
  .Right {
    background-color: #faf9f6;
    border-radius: 20px;
    margin-top: 500px;
    padding: 5px;
    outline: none;
    width: 330px;
    /* width: 100%; */
  }
  hr {
    margin-left: 35px;
    margin-right: 35px;
    padding: 0.4px;
    background-color: #0039a6;
    margin-top: 5px;
    margin-bottom: 3px;
  }
  .ltd {
    font-size: 20px;
    font-family: "Cinzel", serif;
    font-family: "Crimson Text", serif;
    font-family: "Dancing Script", cursive;
    font-family: "Heebo", sans-serif;
    font-family: "Lato", sans-serif;
    font-family: "Montserrat", sans-serif;
    font-family: "Nanum Gothic", sans-serif;
    font-family: "Noto Sans HK", sans-serif;
    font-family: "Open Sans", sans-serif;
    font-family: "Plus Jakarta Sans", sans-serif;
    font-family: "Poppins", sans-serif;
    font-family: "Prompt", sans-serif;
    font-family: "Questrial", sans-serif;
    font-family: "Wix Madefor Text", sans-serif;
    font-weight: 900;
    margin-bottom: 60px;
    margin-top: 40px;
  }
  /*  .LoginInp:focus {
    border: 0px solid;
    border: none;
    background-color: transparent;
  } */
  .LoginInp {
    border: none;
    background-color: transparent;
    border: 0px solid;
    height: 40px;
    width: 260px;
    color: black;
    /* margin-right: 30px; */
    margin-top: 25px;
    font: 1rem "Wix Madefor Text";

    margin-bottom: 0px;
  }

  /* CSS */
  .button-9 {
    appearance: button;
    backface-visibility: hidden;
    background-color: #405cf5;
    border-radius: 6px;
    border-width: 0;
    box-shadow: rgba(50, 50, 93, 0.1) 0 0 0 1px inset,
      rgba(50, 50, 93, 0.1) 0 2px 5px 0, rgba(0, 0, 0, 0.07) 0 1px 1px 0;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    font-family: -apple-system, system-ui, "Segoe UI", Roboto, "Helvetica Neue",
      Ubuntu, sans-serif;
    font-size: 50%;
    height: 40px;
    line-height: 1.15;
    margin: 12px 0 0;
    outline: none;
    overflow: hidden;
    padding: 0 25px;
    position: relative;
    text-align: center;
    text-transform: none;
    transform: translateZ(0);
    transition: all 0.2s, box-shadow 0.08s ease-in;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    width: 30%;
    margin-top: 80px;
    margin-bottom: 20px;
  }

  .button-9:disabled {
    cursor: default;
  }

  .button-9:focus {
    box-shadow: rgba(50, 50, 93, 0.1) 0 0 0 1px inset,
      rgba(50, 50, 93, 0.2) 0 6px 15px 0, rgba(0, 0, 0, 0.1) 0 2px 2px 0,
      rgba(50, 151, 211, 0.3) 0 0 0 4px;
  }
}
